@import url(https://fonts.googleapis.com/css?family=Dax:400,900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.BoxAnimation{margin-left:10em}.BoxAnimation .box{height:1em;width:3em;background-color:white}@-webkit-keyframes pulse{from,40%{opacity:0}50%{opacity:0.5}80%,to{opacity:1}}@keyframes pulse{from,40%{opacity:0}50%{opacity:0.5}80%,to{opacity:1}}

.Train{position:absolute;bottom:10px;left:20%;width:48em;margin-bottom:30px}.Train>div{margin:2em}.Train .coverButton{position:relative;height:3.3em;width:44em}.Train button{outline:none;font:"Dax",sans-serif;font-size:1em;height:3.3em;border:none;padding:0 3em;border-radius:.5em;background-color:white;opacity:1;box-shadow:0 14px 25px rgba(0,0,0,0.123),0 10px 10px rgba(0,0,0,0.012);transition:transform .5s;will-change:transform;position:absolute}.Train button:active{box-shadow:none}.Train .forward{position:absolute;left:0}.Train .backward{position:absolute;right:0}.Train .forwardAnimation{-webkit-animation-name:forward;animation-name:forward;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-timing-function:linear;animation-timing-function:linear;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}.Train .backwardAnimation{-webkit-animation-name:backward;animation-name:backward;-webkit-animation-duration:2s;animation-duration:2s;-webkit-animation-timing-function:linear;animation-timing-function:linear;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes forward{70%,to{transform:translateX(22em)}}@keyframes forward{70%,to{transform:translateX(22em)}}@-webkit-keyframes backward{70%,to{transform:translateX(-22em)}}@keyframes backward{70%,to{transform:translateX(-22em)}}

.Collapse #toggle{display:none;visibility:hidden}.Collapse label{display:inline-block;background-color:rgba(255,255,255,0.3);border-radius:5px;font-family:sans-serif;text-align:center;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);padding:.7em 1em;width:80%;color:black;height:2em;transition:height 1s;transition-timing-function:cubic-bezier(0.98, 0, 0.22, 0.98)}.Collapse label>span{display:inline-block;margin:auto;line-height:2em;transition:transform 1s;transition-timing-function:cubic-bezier(0.98, 0, 0.22, 0.98)}.Collapse label .expand{margin:.8em auto 0 auto;width:80%;height:0;overflow:hidden;transition:height 1s;transition-timing-function:cubic-bezier(0.98, 0, 0.22, 0.98);display:flex;flex-direction:column;align-items:flex-start}.Collapse label .expand>span{transition:opacity .5s;margin:.5em 0;padding-left:.5em;opacity:0}.Collapse #toggle:checked ~ label{height:7em}.Collapse #toggle:checked ~ label>span{transform:translateX(-100%)}.Collapse #toggle:checked ~ label .expand{height:5em}.Collapse #toggle:checked ~ label .expand>span{transition-delay:0.5s;opacity:1}

.Station{width:100vw;display:flex;justify-content:space-around}.Station .eachStation{height:500px;width:200px;background-color:grey;padding:0 50px;display:flex;flex-direction:column;margin:10px;border-radius:10px;background-image:linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);background-image:var(--color, linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%));box-shadow:20px 20px 60px #d9d9d9,  -20px -20px 60px #ffffff}.Station .eachStation>p{border-radius:.4em;padding:.7em;background-color:rgba(255,255,255,0.3);-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);margin:.3em 0}.Station .eachStation>button{margin:5em 0;height:3em;border:none;border-radius:3em;background-color:white;outline:0;transition:transform .2s;will-change:transform;box-shadow:0 14px 25px rgba(0,0,0,0.123),0 10px 10px rgba(0,0,0,0.012)}.Station .eachStation>button span{font-size:1.3em;font-weight:bold;background-image:linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);background-image:var(--color, linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%));-webkit-background-clip:text;background-clip:text;-webkit-text-fill-color:transparent}.Station .eachStation>button:active{box-shadow:none;transform:scale(0.9)}.Station .green{--color: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%)}.Station .yellow{--color: linear-gradient(120deg, #f6d365 0%, #fda085 100%)}.Station .red{--color: linear-gradient(120deg, #f093fb 0%, #f5576c 100%)}.Station .zero{background-color:#87DCC0}.Station .one{background-color:#88BBE4}

body{display:grid;place-items:center;height:100vh}body .TrainStation{position:relative;display:flex;justify-content:center;flex-direction:column}body #getData{background-color:rgba(205,241,175,0.37);display:grid;place-items:center;padding:10px}

